import ApiService from "../common/api.service";
import AuthService from "../common/auth.service";
import {
    Login,
    Register,
    CheckAuth,
    UpdateUser
} from "./actions.type";
import { Authorized, ErrorOccured } from "./mutation.type";

const state = {
    errors: null,
    user: {},
    isAuthenticated: !!AuthService.getToken()
};

const getters = {
    currentUser(state) {
        return state.user;
    },
    isAuthenticated(state) {
        return state.isAuthenticated;
    }
};

const actions = {
    async [Login](context, credentials) {
        try {
            let data = await ApiService.post("users/login", {user: credentials});
            context.commit(Authorized, data.user);
            return data;
        } catch (response) {
            context.commit(ErrorOccured, response.data.errors);
        }
    },
    [Register](context, credentials) {

    },
    [CheckAuth](context) {

    }
};

export default {
    state,
    getters
}