const UserGuio = "id_token";

export const getToken = () => {
    return window.localStorage.getItem(UserGuio);
};

export const saveToken = token => {
    window.localStorage.setItem(UserGuio, token);
};

export const destroyToken = () => {
    window.localStorage.removeItem(UserGuio);
};

export default { getToken, saveToken, destroyToken };
